import React, { Component } from 'react'
import './Caculator.css'
export default class Caculator extends Component {
    state = {
        click: false,
        result: "0"
    }
    clickKey = async (key) => {
        if (!this.state.click) {
            await this.setState({
                click: true,
                result: ""
            })
        }
        let checkLast = RegExp(/[0-9]/);
        if (checkLast.test(key)) {
            await this.setState({
                result: this.state.result + key
            })
        } else {
            //Nếu kí tự cuối cùng trong result là số thì vẫn thêm + - * / .
            if (checkLast.test(this.state.result[this.state.result.length - 1])) {
                await this.setState({
                    result: this.state.result + key
                })
            } else {
                //Nếu kí tự cuối cùng trong result  không là số mà là + - * / . thì thay đổi lại toán tử => key
                await this.setState({
                    result: this.state.result.slice(0, this.state.result.length - 1) + key
                })
            }
        }
    }
    delete = () => {
        if (this.state.click) {
            this.setState({
                result: this.state.result.slice(0, this.state.result.length - 1)
            })
        }
    }
    sum = () => {
        if (this.state.result !== "") {
            this.setState({
                result: eval(this.state.result).toString()
            })
        } else {
            this.resetResult();
        }
    }
    resetResult = () => {
        this.setState({
            click: false,
            result: "0"
        })
    }
    render() {
        return (
            <div className="container">
                <div className="row">
                    <div className="caculator">
                        <div className="caculator__content">
                            <div className="caculator__result">
                                <input type="text" className="caculator__input" value={this.state.result} disabled />
                            </div>
                            <div className="caculator__key">
                                <button onClick={() => { this.clickKey("7") }}>7</button>
                                <button onClick={() => { this.clickKey("8") }}>8</button>
                                <button onClick={() => { this.clickKey("9") }}>9</button>
                                <button onClick={() => { this.clickKey("/") }}>/</button>
                                <button onClick={() => { this.resetResult() }}>CE</button>
                                <button onClick={() => { this.clickKey("4") }}>4</button>
                                <button onClick={() => { this.clickKey("5") }}>5</button>
                                <button onClick={() => { this.clickKey("6") }}>6</button>
                                <button onClick={() => { this.clickKey("*") }}>*</button>
                                <button onClick={() => { this.delete() }}>C</button>
                                <button onClick={() => { this.clickKey("1") }}>1</button>
                                <button onClick={() => { this.clickKey("2") }}>2</button>
                                <button onClick={() => { this.clickKey("3") }}>3</button>
                                <button onClick={() => { this.clickKey("-") }}>-</button>
                                <button onClick={() => { this.sum() }}>=</button>
                                <button onClick={() => { this.clickKey("0") }}>0</button>
                                <button onClick={() => { this.clickKey(".") }}>.</button>
                                <button onClick={() => { this.clickKey("+") }}>+</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
